/* write your css here */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

body {
  min-height: 100vh;
  max-width: 100vw;
  overflow-x: hidden;
}

.container {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.title {
  font-size: 3rem;
  margin: 6vh auto;
  border: 7px dashed rgb(1, 17, 160);
  border-radius: 20px;
  padding: 1vw 5vh;
  text-align: center;
}

.cards {
  display: flex;
  justify-content: center;
  /* align-items: center; */
  flex-wrap: wrap;
  max-width: 1300px;
  margin: 0 auto;
}

.card {
  width: 400px;
  height: max-content;
  margin: 1rem;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  /* align-items: center; */
  box-shadow: rgba(0, 0, 0, 0.4) 0 3px 8px;
}

.image {
  width: 380px;
  aspect-ratio: 1/1;
  object-fit: cover;
}

.tour-details {
  margin-top: 20px;
  text-align: left;
}

.tour-price {
  color: #1faa59;
  font-size: 1.3rem;
}

.tour-name {
  font-size: 1.5rem;
}
.description {
  font-size: 1.1rem;
}

.readmore {
  color: #12b0e8;
  cursor: pointer;
}

.btn-red {
  width: 100%;
  margin-top: 18px;
  padding: 10px 80px;
  border: 1px solid #b4161b;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
  background-color: #b4161b21;
}

.btn-red:hover {
  background-color: red;
  color: white;
  transition: all 0.3s;
}

.refersh {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.refersh h4 {
  font-size: 1.5rem;
}

.btn-white {
  margin-top: 18px;
  padding: 10px 80px;
  font-weight: bold;
  border: 1px solid white;
  cursor: pointer;
}

.btn-white:hover {
  background-color: #fff;
  transition: all 0.3s;
}

@media screen and (max-width: 500px) {
  .title {
    font-size: 2rem;
    border: 4px dashed rgb(1, 17, 160);

  }
}
